<template>
  <b-row>
    <b-col md="12">
      <b-card no-body :header="caption" style="margin-bottom:5px;">
        <b-table class="mb-0" responsive="sm" hover :items="items" :fields="fields" :current-page="currentPage" :per-page="perPage">
          <div slot="identicon" class="identicon" slot-scope="data">
            <Blockie :address="data.item.address" size="small"/>
          </div>
          <div slot="address" slot-scope="data">
            <div><a :href="'https://ubiqscan.io/address/' + data.value" target="_blank">{{data.value}}</a></div>
          </div>
          <div slot="balance" slot-scope="data">
            <strong>{{data.value}}</strong>
          </div>
          <div slot="escher" slot-scope="data">
            <strong>{{escher(data.item.balance)}}</strong>
          </div>
        </b-table>
      </b-card>
      <nav style="margin-right:-4px;">
        <b-pagination size="md" align="right" :total-rows="getRowCount(items)" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next"/>
      </nav>
    </b-col>
  </b-row>
</template>

<script>
  import Blockie from '../Blockie.vue'
  export default {
    props: {
      caption: {
        type: String,
        default: '<i class="fa fa-check icon-left"></i>Claims'
      },
      items: {
        type: Array,
        default: []
      },
      escher: {
        type: Function
      }
    },
    data: () => {
      return {
        currentPage: 1,
        perPage: 10,
        totalRows: 0,
        fields: {
          identicon: {
            label: '<i class="fa fa-users">',
            class: 'text-center identicon-column'
          },
          address: {
            label: 'Address'
          },
          balance: {
            label: 'Balance (UBQ)',
            class: 'text-right'
          },
          escher: {
            label: 'Escher (ESCH)',
            class: 'text-right'
          }
        }
      }
    },
    methods: {
      getRowCount (items) {
        return items.length
      }
    },
    components: {
      Blockie
    }
  }
</script>



